}

.name {
  position: relative;
  -webkit-box-reflect: below -20px linear-gradient(transparent, rgba(0, 0, 0, 0.2));
  font-size: 60px;
}

@-webkit-keyframes name {
  0%,
  60%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  20% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
}

@keyframes name {
  0%,
  60%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  20% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
}

@-webkit-keyframes up {
  to {
    -webkit-transform: rotate(360dg);
            transform: rotate(360dg);
  }
}

@keyframes up {
  to {
    -webkit-transform: rotate(360dg);
            transform: rotate(360dg);
  }
}

@-webkit-keyframes down {
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes down {
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

.l {
  display: inline-block;
  -webkit-animation: name 2s;
          animation: name 2s;
  -webkit-animation-delay: calc(0.1s * var(--i));
          animation-delay: calc(0.1s * var(--i));
}

.name span {
  transition: -webkit-transform 1550ms;
  transition: transform 1550ms;
  transition: transform 1550ms, -webkit-transform 1550ms;
}
.name span:hover {
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  -webkit-animation: name 3s;
          animation: name 3s;
}

.homepage h2 {
  position: relative;
}


.appear {
  text-align: center;
  width: 55%;
}

.welcome {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: absolute;
  left: 40%;
  overflow: hidden;
  -webkit-animation: animate 2s linear forwards;
          animation: animate 2s linear forwards;
}

.welcome h2 {
  color: black;
  border-bottom: 1px solid black;
}

@-webkit-keyframes animate {
  0% {
      width: 0px;
      height: 0px;
  }
  30% {
      width: 50px;
      height: 0px;
  }
  60% {
      width: 50px;
      height: 80px;
  }
}

@keyframes animate {
  0% {
      width: 0px;
      height: 0px;
  }
  30% {
      width: 50px;
      height: 0px;
  }
  60% {
      width: 50px;
      height: 80px;
  }
}
:root {
  --heavyblue: #2e3047;
  --mediumblue: #3c3f58;
  --lightblue: #707793;
  --mint: #3bba9c;
  --gold: #ffd700;
  --main: #43455c;
  --jeanblue: #5D76A9;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  html {
    scroll-behavior: smooth;
  }
  
  body {
    color: white;
    background-color: #3c3f58;
    background-color: var(--mediumblue);
    font-family: "Quicksand", sans-serif;
    height: 100%;
    scroll-behavior: smooth 1s;
  }
  
  main {
    transition: padding-left 0.5s;
    justify-content: center;
    height: 100%;
    align-items: center;
  }
  
  /*------------  Scroll Properties  ------------*/
section {
    height: 100vh;
    scroll-snap-align: center;
    transition: all ease-in-out 1s;
  }
  
  .one {
    background-color: white;
  }
  
  .two {
    background-color: #5D76A9;
    background-color: var(--jeanblue);
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  
  .three {
    background-color: #43455c;
    background-color: var(--main);
  }
  
  .container {
    scroll-snap-type: y proximity;
    height: 100vh;
    overflow-y: scroll;
  }

  /*------------  Welcome Section Styling  ------------*/

.welcome {
  display: flex;
  justify-content: center;
  width: 100%;
  position: relative;
  place-items: center;
  margin-top: 100px;
  color: black;
}

  /* For Desktop View */
@media screen and (min-width: 1024px) {
  .root {
    background-color: #63c971;
    color: #fff;
  }
}
 
/* For Tablet View */
@media screen and (min-device-width: 768px)
and (max-device-width: 1024px) {
  .root {
    width: 400px;
    height: 400px;
    background-color: orange;
    color: black;
  }
}
 
/* For Mobile Portrait View */
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .root {
    width: 200px;
    height: 200px;
    background-color: red;
    color: #fff;
  }
}
 
/* For Mobile Landscape View */
@media screen and (max-device-width: 640px)
and (orientation: landscape) {
  .root {
    width: 400px;
    height: 200px;
    background-color: cyan;
    color: black;
  }
}
 
/* For Mobile Phones Portrait or Landscape View */
@media screen and (max-device-width: 640px) {
  .root {
    width: 400px;
    height: 200px;
    background-color: chartreuse;
    color: black;
  }
}
 
/* For iPhone 4 Portrait or Landscape View */
@media screen and (min-device-width: 320px)
and (-webkit-min-device-pixel-ratio: 2) {
  .root {
    width: 400px;
    height: 400px;
    background-color: brown;
    color: black;
  }
}
 
/* For iPhone 5 Portrait or Landscape View */
@media (device-height: 568px) and (device-width: 320px)
and (-webkit-min-device-pixel-ratio: 2) {
  .root {
    width: 400px;
    height: 400px;
    background-color: cornflowerblue;
    color: black;
  }
}
 
/* For iPhone 6 and 6 plus Portrait or Landscape View */
@media (min-device-height: 667px) and (min-device-width: 375px)
and (-webkit-min-device-pixel-ratio: 3) {
  .root {
    width: 400px;
    height: 400px;
    background-color: darkgoldenrod;
    color: black;
  }
}

